body {
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}

.App {
  text-align: center;
}

.counter {
  margin: auto;
  width: 200px;
  background-color: #ffffff;
  border-radius: 8px;
  padding: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.count-display {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333333; /* Change the color of the count display text */
}

.buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  background-color: #4caf50;
  color: #ffffff;
  border-radius: 5px;
  transition: background-color 0.3s;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

button:hover {
  background-color: #45a049;
}

.error-message {
  color: #ff0000;
  font-size: 14px;
  margin-top: 10px;
}
